import React from 'react';
import OtherMoreCarousel from '../OtherMoreCarousel/OtherMoreCarousel';
import Line from '../Line/Line';
import classes from './OtherMore.module.scss';
function OtherMore() {
	return (
		<div className="container">
			<div className="row">
				<div className="col-lg-6 col-md-6 col-sm-12 col-12">
					<OtherMoreCarousel />
				</div>
				<div className="col-lg-6 col-md-6 col-sm-12 col-12">
					<p className={classes.text}>
						<div className="d-flex justify-content-start">
							<Line />
						</div>
						ارائه دهنده ی سیستم آبیاری خودکار دیجیتالی و مشاوره ی رایگان درجهت راهنمایی کشت و جلوگیری از
						آسیب های آفت ها و حشرات موذی
					</p>
				</div>
			</div>
		</div>
	);
}

export default OtherMore;
