import React from 'react';
import Slider from 'react-slick';
import classes from './OtherMoreCarousel.module.scss';
import one from '../../img/5.jpg';
import two from '../../img/6.jpg';
import three from '../../img/7.jpg';

function OtherMoreCarousel() {
	const settings = {
		slidesToShow: 1,
		slidesToScroll: 1,
		infinite: true,
		speed: 800,
		autoplay: true,
		autoplaySpeed: 3500,
		fade: true,
		arrows: false
	};
	return (
		<Slider className="mr-5 ml-5 pt-5" {...settings}>
			<div className={classes.link}>
				<div className={classes.cont}>
					<div className="d-flex flex-column">
						<div className="d-flex justify-content-center">
							<img alt="img" className={classes.img} src={one} />
						</div>
					</div>
				</div>
			</div>
			<div className={classes.link}>
				<div className={classes.cont}>
					<div className="d-flex flex-column">
						<div className="d-flex justify-content-center">
							<img alt="img" className={classes.img} src={two} />
						</div>
					</div>
				</div>
			</div>

			<div className={classes.link}>
				<div className={classes.cont}>
					<div className="d-flex flex-column">
						<div className="d-flex justify-content-center">
							<img alt="img" className={classes.img} src={three} />
						</div>
					</div>
				</div>
			</div>
		</Slider>
	);
}

export default OtherMoreCarousel;
